@tailwind base;
@tailwind components;
@tailwind utilities;

/* Custom Theme Variables */
:root {
  --color-coral: #E19393;
  --color-coral-light: #f7d8d8;
  --color-coral-dark: #b86565;
  --color-coral-darker: #9a4a4a;
  --color-cream: #FDF6ED;
  --color-cream-light: #fefaf5;
}

/* Font Imports */
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@600&display=swap');

/* Custom Utilities */
.font-script {
  font-family: 'Dancing Script', cursive;
}

.text-coral {
  color: var(--color-coral);
}

.text-coral-light {
  color: var(--color-coral-light);
}

.text-coral-dark {
  color: var(--color-coral-dark);
}

.text-coral-darker {
  color: var(--color-coral-darker);
}

.bg-cream {
  background-color: var(--color-cream);
}

.bg-cream-light {
  background-color: var(--color-cream-light);
}

.border-coral-light {
  border-color: var(--color-coral-light);
}
